import React, { useState, useRef, useEffect } from 'react'

/**
 * Settings
 */
const pluginApi = 'https://utteranc.es/client.js'
const websiteRepo = 'AurelienDud/AurelienDud.github.io'
const githubLabel = 'comments'
const githubTitle = 'pathname'
const commentsTheme = 'boxy-light'

/**
 * Override Utterances styles
 * this is ugly but the plugin seems not provide a way to customize its container styles
 * and set an arbitrary max-width
 */
const forceStyles = `
  .utterances {
    max-width: 100% !important;
  }
`

/**
 * Comments component
 */
function Comments () {
  const comments = useRef(null)
  const [isReady, setIsReady] = useState(false)

  useEffect(() => {
    if (comments?.current && !isReady) {
      const scriptTag = document.createElement('script')
      scriptTag.async = true
      scriptTag.src = pluginApi
      scriptTag.setAttribute('repo', websiteRepo)
      scriptTag.setAttribute('issue-term', githubTitle)
      scriptTag.setAttribute('theme', commentsTheme)
      scriptTag.setAttribute('label', githubLabel)
      scriptTag.setAttribute("async", true);
      scriptTag.setAttribute('crossorigin', 'anonymous')
      scriptTag.setAttribute('reverse-order', true)
      scriptTag.setAttribute('input-position-top', true)

      comments.current.appendChild(scriptTag)
      setIsReady(true)
    }
  }, [comments])

  return (
    <>
      <style>
        {forceStyles}
      </style>
      <div className="w-100" ref={comments} />
    </>
  )
}

export default Comments