import React, { useRef, useEffect } from "react";
import { cx, css } from "@emotion/css";
import { graphql } from "gatsby";
import { GatsbyImage, getImage } from "gatsby-plugin-image";
import { MDXProvider } from "@mdx-js/react";
import { MDXRenderer } from "gatsby-plugin-mdx";
import mdxComponents from "../services/mdxConvertor";
import { formatBlogData } from "../services/formaters";
import { getCategoryRoute } from "../services/routes";
import Layout from "./layout-page";
import Link from "../components/link";
import Comments from "../components/comments";

/**
 * Settings
 */
const parallaxStrength = 1.25;

/**
 * QUery
 */
export const pageQuery = graphql`
  query($slug: String!) {
    mdx(fields: { slug: { eq: $slug } }) {
      id
      body
      frontmatter {
        title
        category
        date
        cover {
          childImageSharp {
            gatsbyImageData(layout: FULL_WIDTH)
          }
        }
      }
    }
  }
`;

/**
 * Styles
 */
const coverStyle = css`
  position: relative;
  overflow: hidden;
  box-shadow: 0px -40px 40px -40px inset #00000030;
  &:after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    border-top: solid 2vw transparent;
    border-bottom: solid 2vw white;
    border-left: solid 50vw white;
    border-right: solid 50vw white;
  }
`;

const coverImgStyles = css`
  position: relative;
  border-radius: 5px;
  border-top: solid 6px white;
  border-right: solid 6px white;
  border-left: solid 6px white;
  transform-origin: center;
  transform: translate3d(0, 0, 0);
  -webkit-transform-style: preserve-3d;
  -webkit-backface-visibility: hidden;
  transition: all 0s 0s;
  background-color: #ffffff30;
  opacity: 1;
  transition: opacity linear 0.4s;
  box-shadow: rgb(0 0 0 / 33%) 0px -45px 20px -20px inset;
`;

/**
 * View
 * @param {object} props
 */
export default function Template({ data }) {
  // format mdx data
  const model = formatBlogData(data.mdx);

  // metadata
  const meta = {
    type: "article",
    category: model.category
  }

  // set a ref on the cover container to create the visual effect
  const coverEl = useRef(null);

  // set a ref to manage the parallax effect
  const parallax = useRef({
    scroll: 0,
    mov: 0,
    elBound: null,
  });

  // init parallax
  useEffect(() => {
    if (!model.cover || !coverEl.current) return;

    if (!parallax.current.elBound)
      parallax.current.elBound = coverEl.current.getBoundingClientRect();
  }, [coverEl, model]);

  // manage parallax effect
  useEffect(() => {
    if (!model.cover || !coverEl.current) return;

    // handle event
    function handleScroll(event) {
      event.preventDefault();

      if (
        !parallax.current.elBound ||
        window.scrollY > parallax.current.elBound.height
      )
        return;

      const scroll = Math.max(0, Math.round(window.scrollY * parallaxStrength));
      const mov = Math.round(scroll - parallax.current.scroll);
      const opacity =
        1 - (1 / parallax.current.elBound.height) * scroll * parallaxStrength;

      if (
        Math.sign(mov) === Math.sign(parallax.current.mov) ||
        parallax.current.scroll === 0
      ) {
        coverEl.current.style.transform = `translate3d(0, ${scroll}px, 0)`;
        coverEl.current.style.opacity = opacity;
      }

      parallax.current.scroll = scroll;
      parallax.current.mov = mov;
    }

    // create event
    window.addEventListener("scroll", handleScroll);

    // remove event
    return () => {
      window.removeEventListener("scroll", handleScroll);
    };
  }, [model]);

  return (
    <Layout meta={meta}>
      {/* cover */}
      {model.cover && (
        <div style={{ overflow: "hidden" }}>
          <div className={cx(coverStyle, "bg-primary")}>
            <div className="container-reading">
              <div className={coverImgStyles} ref={coverEl}>
                <GatsbyImage image={getImage(model.cover)} alt="" />
              </div>
            </div>
          </div>
        </div>
      )}

      {/* content */}
      <div className="container mt-4 mb-3">
        {(model.title || model.category) && (
          <div className="h1 mb-4 text-center">
            {/* category */}
            {model.category && (
              <Link to={getCategoryRoute(model.category)} className="badge badge-secondary mb-1 decoration-unset">
                {model.category}
              </Link>
            )}

            {/* title */}
            {model.title && <h1 className="h-0">{model.title}</h1>}
          </div>
        )}

        <div className="container-reading mt-3">
          {/* date */}
          {model.date && (
            <p className="text-muted small">publi√© le {model.date}</p>
          )}

          {/* body */}
          {model.body && (
            <MDXProvider components={mdxComponents}>
              <MDXRenderer>{model.body}</MDXRenderer>
            </MDXProvider>
          )}
        </div>

        <div className="mt-4">
          <Comments />
        </div>
      </div>
    </Layout>
  );
}
